/* 公共组件状态机
 * 作者：平原
 * 2017-6-14 */

const state = {
  // 分页组件状态
  pagination: {
    showPagination: false,
    pageItem: [],
    curPage: 1,
    preOrNext: 0
  },
  curPage: 1,

  // 加载动画组件状态
  loading: {
    isShow: false,
    id: ''
  }
}

const mutations = {
  INIT_PAGINATION (state, { total, page, limit }) {
    let pageCount = Math.ceil(total / limit)
    let newState = {
      curPage: page,
      showPagination: pageCount > 1,
      pageItem: [],
      preOrNext: 0
    }

    for (let i = 0; i < pageCount; i++) {
      if (i === page - 1) {
        newState.pageItem.push({'page': i + 1, 'active': 1})
      } else {
        newState.pageItem.push({'page': i + 1})
      }
    }

    // 判断是否是第一页或最后一页disabled
    if (page === 1) {
      newState.preOrNext = -1
    } else if (page === pageCount) {
      newState.preOrNext = 1
    }
    state.pagination = Object.assign({}, newState)
  },

  UPDATE_PAGE (state, toPage) {
    if (toPage) {
      state.curPage = toPage
    }
  },

  LOADING_SHOW (state, data) {
    state.loading = Object.assign({}, data)
  }
}

const actions = {
  initPagination ({ commit }, data) {
    commit('INIT_PAGINATION', data)
  },

  updatePage ({commit}, toPage) {
    commit('UPDATE_PAGE', toPage)
  },

  loadingShow: ({ commit }, loadingId) => {
    commit('LOADING_SHOW', { id: loadingId, isShow: true })
  },

  loadingHide: ({ commit }, loadingId) => {
    commit('LOADING_SHOW', { id: loadingId, isShow: false })
    // const setCommit = () => {
    //   commit('LOADING_SHOW', { id: loadingId, isShow: false })
    // }

    // if (delay) { // 弹框支持延迟隐藏
    //   clearTimeout()
    //   setTimeout(() => {
    //     setCommit()
    //   }, delay)
    //   return
    // }

    // setCommit()
  }
}

export default {
  state,
  mutations,
  actions
}



